<template>
  <div class="NewIteration">
    <el-button class="btn btn-primary" type="text" @click.stop="newIteration">新建迭代</el-button>
    <el-dialog
      title="新建迭代"
      :visible.sync="dialogFormVisible"
      :modal-append-to-body="false"
      :append-to-body="true"
    >
      <el-form :model="form" :rules="rules" ref="ruleForm">
        <el-form-item label="迭代名称" label-width="120px" prop="name">
          <el-input v-model="form.name" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="所属项目" label-width="120px" prop="project">
          <el-input v-model="form.project" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="迭代时间" label-width="120px" required>
          <el-col :span="11">
            <el-form-item prop="date1">
              <el-date-picker
                type="date"
                placeholder="选择日期"
                v-model="form.date1"
                style="width: 100%;"
              ></el-date-picker>
            </el-form-item>
          </el-col>
          <el-col class="line" :span="2">-</el-col>
          <el-col :span="11">
            <el-form-item prop="date2">
              <el-date-picker
                type="date"
                placeholder="选择日期"
                v-model="form.date2"
                style="width: 100%;"
              ></el-date-picker>
            </el-form-item>
          </el-col>
        </el-form-item>
        <el-form-item label="迭代目标" label-width="120px" prop="aims">
          <el-input type="textarea" autosize placeholder="请输入内容" v-model="form.aims"></el-input>
        </el-form-item>
        <el-form-item style="margin-left:100px">
          <el-button type="primary" @click.stop="submitForm('ruleForm')">立即创建</el-button>
          <el-button @click.stop="resetForm('ruleForm')">重置</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      dialogFormVisible: false,
      form: {
        name: "",
        project: "",
        date1: "",
        date2: "",
        name: "",
        aims: ""
      },
      rules: {
        name: [{ required: true, message: "请输入迭代名称", trigger: "blur" }],
        project: [
          { required: true, message: "请输入所属项目", trigger: "blur" }
        ],
        aims: [{ required: true, message: "请输入迭代目标", trigger: "blur" }],
        date1: [
          {
            type: "date",
            required: true,
            message: "请选择日期",
            trigger: "change"
          }
        ],
        date2: [
          {
            type: "date",
            required: true,
            message: "请选择日期",
            trigger: "change"
          }
        ]
      }
    };
  },
  methods: {
    newIteration() {
      this.dialogFormVisible = true;
    },
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          alert("submit!");
          this.dialogFormVisible = false;
          this.$message({
            message: "恭喜你，这是一条成功消息",
            type: "success"
          });
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    }
  }
};
</script>

<style lang="scss" scoped>
</style>