<template>
  <div>
    <el-button type="text" class="btn btn-primary" style="float:right" @click="NewProject">新建项目</el-button>
    <el-dialog
      title="新建项目"
      :visible.sync="dialogFormVisible"
      :modal-append-to-body="false"
      :append-to-body="true"
    >
      <el-form
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item label="项目名称" prop="name">
          <el-input v-model="ruleForm.name"></el-input>
        </el-form-item>
        <el-form-item label="项目类型" prop="types">
          <el-select v-model="ruleForm.types" placeholder="请选择项目类型">
            <el-option label="研发" value="yanfa"></el-option>
            <el-option label="测试" value="ceshi"></el-option>
            <el-option label="维护" value="weihu"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="交付部" prop="department">
          <el-select v-model="ruleForm.department" placeholder="请选择交付部">
            <el-option label="第一个交付部" value="first"></el-option>
            <el-option label="第二个交付部" value="second"></el-option>
            <el-option label="第三个交付部" value="third"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="商务类型" prop="business">
          <el-select v-model="ruleForm.business" placeholder="请选择商务类型">
            <el-option label="商务类型1" value="business1"></el-option>
            <el-option label="商务类型2" value="business2"></el-option>
            <el-option label="商务类型3" value="business3"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="归属地区" prop="position">
          <el-select v-model="ruleForm.position" placeholder="请选择归属地区">
            <el-option label="成都" value="chengdu"></el-option>
            <el-option label="西安" value="xian"></el-option>
            <el-option label="深圳" value="shenzhen"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="项目时间" required>
          <el-col :span="11">
            <el-form-item prop="date1">
              <el-date-picker
                type="date"
                placeholder="开始时间"
                v-model="ruleForm.date1"
                style="width: 100%;"
              ></el-date-picker>
            </el-form-item>
          </el-col>
          <el-col class="line" :span="2">-</el-col>
          <el-col :span="11">
            <el-form-item prop="date2">
              <el-date-picker
                type="date"
                placeholder="开始时间"
                v-model="ruleForm.date2"
                style="width: 100%;"
              ></el-date-picker>
            </el-form-item>
          </el-col>
        </el-form-item>

        <el-form-item>
          <el-button type="primary" @click.stop="submitForm('ruleForm')">立即创建</el-button>
          <el-button @click.stop="resetForm('ruleForm')">重置</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>

    <el-dialog title="提示" :visible.sync="exportMember" width="30%">
      <span>是否立即导入成员</span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="jumpProjectList">取 消</el-button>
        <el-button type="primary" @click="jumpMember">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      exportMember:false,
      dialogFormVisible: false,
      ruleForm: {
        name: "",
        types: "",
        date1: "",
        date2: "",
        business: "",
        position: "",
        department: ""
      },
      rules: {
        name: [{ required: true, message: "请输入项目名称", trigger: "blur" }],
        department: [
          { required: true, message: "请选择交付部", trigger: "change" }
        ],
        business: [
          { required: true, message: "请选择商务类型", trigger: "change" }
        ],
        position: [
          { required: true, message: "请选择归属地区", trigger: "change" }
        ],
        types: [
          { required: true, message: "请选择项目类型", trigger: "change" }
        ],
        date1: [
          {
            type: "date",
            required: true,
            message: "请选择日期",
            trigger: "change"
          }
        ],
        date2: [
          {
            type: "date",
            required: true,
            message: "请选择时间",
            trigger: "change"
          }
        ]
      }
    };
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.dialogFormVisible = false;
          this.$message({
            message: "恭喜你，创建成功",
            type: "success"
          });
          this.exportMember=true
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    jumpProjectList(){
      this.exportMember=false
      this.$router.push('/Main/ViewProject')
    },
    jumpMember(){
      this.exportMember=false
      this.$router.push('/Main/Member')
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    NewProject() {
      this.dialogFormVisible = true;
    }
  }
};
</script>

<style lang="less" scoped>
</style>